
Template.__checkName("MeteorToys_status");
Template["MeteorToys_status"] = new Template("Template.MeteorToys_status", (function() {
  var view = this;
  return HTML.DIV({
    class: "MeteorToys_orb MeteorToys_button",
    id: "MeteorToys_status",
    oncontextmenu: "Package['meteortoys:toykit'].MeteorToys.closeToy(); return false;"
  }, "\t\n\t\t", Blaze.If(function() {
    return Spacebars.call(view.lookup("online"));
  }, function() {
    return [ "\n\t\t\t", HTML.DIV({
      class: "MeteorToys_icon MeteorToys_icon_online"
    }), "\n\t\t" ];
  }), "\n\t\t", Blaze.If(function() {
    return Spacebars.call(view.lookup("connecting"));
  }, function() {
    return [ "\n\t\t\t", HTML.DIV({
      class: "MeteorToys_icon MeteorToys_icon_connecting"
    }), "\n\t\t" ];
  }), "\n\t\t", Blaze.If(function() {
    return Spacebars.call(view.lookup("offline"));
  }, function() {
    return [ "\n\t\t\t", HTML.DIV({
      class: "MeteorToys_icon MeteorToys_icon_offline"
    }), "\n\t\t" ];
  }), "\n\t\t", Blaze.If(function() {
    return Spacebars.call(view.lookup("waiting"));
  }, function() {
    return [ "\n\t\t\t", HTML.DIV({
      class: "MeteorToys_icon MeteorToys_icon_waiting"
    }), "\n\t\t" ];
  }), "\n\t\t", Blaze.If(function() {
    return Spacebars.call(view.lookup("failed"));
  }, function() {
    return [ "\n\t\t\t", HTML.DIV({
      class: "MeteorToys_icon MeteorToys_icon_failed"
    }), "\n\t\t" ];
  }), HTML.Raw('\n\t\t<div class="MeteorToys_orb_filler_wrapper"></div>\n\t'));
}));
